import React, { useEffect } from 'react';
import style from '../Style/DefaultSection.module.css';
import image1 from '../img/defaultimg1.png';

const DefaultSection = () => {
  useEffect(() => {
    const strongSection = document.querySelector(`.${style.StrongSection}`);
    const addAnimations = () => {
      const windowHeight = window.innerHeight;
      const strongSectionTop = strongSection.getBoundingClientRect().top;

      if (strongSectionTop <= windowHeight / 2) {
        strongSection.classList.add(style.fadeInFromBottom);
        strongSection.classList.add(style.slideInFromTop);
        window.removeEventListener('scroll', addAnimations);
      }
    };

    window.addEventListener('scroll', addAnimations);

    return () => {
      window.removeEventListener('scroll', addAnimations);
    };
  }, []);

  return (
    <div className={style.container}>
      <div className={style.firstSection}>
        <div className={style.Introduce}>
          <h2 className={style.SubTitle}>
            <span className={style.SubStrong}>기획/컨설팅 분야,</span>
            <br /> 국내 최고의 전국대학연합 동아리,
          </h2>
          <h1 className={style.Title}>
            <span className={style.strongDesign}>팔랑크스</span>에 오신 것을 환영합니다.
          </h1>
        </div>

        <div className={style.AboutClub}>
          <div className={style.imgSection1}>
            <img
              className={style.FirstImage}
              alt="IntroImage_Phalanx"
              src={image1}
            />
          </div>
          <p className={style.strongDesign}>팔랑크스는,</p>

          <p>
            IT / 마케팅 / 신사업 3개 분야를 중심으로
            <br />
            모든 크루가&nbsp;
            <span style={{ color: 'rgba(255, 238, 0, 0.788)' }}>
              자신의 기획(개인+파트별)을
            </span>
            <br />
            주차별 커리큘럼에 따라 진행하는&nbsp;
            <span style={{ color: 'rgba(255, 238, 0, 0.788)' }}>
              30주 과정
            </span>
            으로,
            <br />
            <br />
            <span style={{ color: 'rgba(255, 238, 0, 0.788)' }}>
              4주당 1개씩 자신이 관심이 있는 기업과 서비스에게
              <br />
              나와 내가 소속된 파트의 기획서를 발송하여, <br />
              실무 프로젝트를 타진하는 과정
            </span>
            을 핵심으로 진행하는<br />
            국내 최고의 기획 분야 동아리(클럽) 입니다. ☺
          </p>
        </div>
      </div>

      <div className={style.secondSection}>
        <div className={style.SloganSection}>
          <p>학교에서 과제로 하는 레포트, 팀플이 아닌</p>
          <p className={style.SubSlogan}>진짜..기업에게 던지는 기획!</p>
          <p>우리 또래의 아이디어와 망상에만 머무르지 말고,</p>
          <p className={style.SubSlogan}>진짜..실무자의 기획으로 바꿔보자!</p>
          <p>재미있는 상상으로 구성된 메모가 아닌,</p>
          <p className={style.SubStrong}>치열한 구조화로 도출된 현실적 기획</p>
        </div>

        <div className={style.StrongSection}>
          <h1 className={style.StrongSlogan}>
            "모든 건<br /> 기획이다!""
          </h1>
          <img
            className={style.SecondImage}
            alt="IntroImage_Phalanx"
            src={image1}
          />
        </div>
      </div>
    </div>
  );
};

export default DefaultSection;
